.h-section__title {
    &.h-section__title_practices {
        font-size: 70px;
        line-height: 85px;
        margin-bottom: 70px;
    }
}

.h-section__info {
    &.h-section__info_practices {
        margin-top: 15px;
    }
}

.h-section__video-block {
    max-width: 80%;
    margin-left: 30px;
}

.video-block {
    width: 100%;
}

.video-block__toggler {
    display: none;
}

.video-block__list-c {
    &:after {
        position: absolute;
        display: block;
        content: "";
        top: 0;
        bottom: 30px;
        width: 30%;
        background: linear-gradient(to left, rgb(13, 13, 73) 0%, rgba(13, 13, 73, 0) 100%);
        right: -2px;
        opacity: 0;
        transition: 0.3s;
        pointer-events: none;
    }

    &_shadow {
        &:after {
            opacity: 1;
        }
    }

    .video-block__list-c {
        &_shadow {
            &:after {
                display: none;
            }
        }
    }
}

.video-block__list {
    display: flex;
    padding-bottom: 30px;
    position: relative;
}

.video-block__item {
    flex: 0 0 auto;
    width: calc(33.333333333% - 14px);
    margin-right: 20px;

    &:last-child {
        margin-right: 0;
    }
}

.video-block__link {
    display: block;
}

.video-block__img-container {
    background: #fff;
    border-radius: 7px;
    padding-top: 56.5%;
    margin-bottom: 8px;
    background-size: cover;
    background-position: center;
}

.video-block__title {
    font-size: 12px;
    line-height: 16px;
    color: #fff;
    max-height: 33px;
    overflow: hidden;
}

.h-section-practices__col-r {
    position: relative;

    &:after {
        position: absolute;
        display: block;
        content: "";
        width: 100%;
        height: 90px;
        bottom: 0;
        left: 10px;
        right: 10px;
        background: linear-gradient(to top, #0d0d49 0%, rgba(#0d0d49, 0) 100%);
    }
}

.h-section-practices__col-r-in {
    overflow-x: hidden;
    overflow-y: auto;
    margin-right: -100px;
    padding-right: 140px;
}

.h-section-practices__col-r-full-row {
    max-height: none;
    height: auto;
}

.practices-description {
    color: #fff;
    margin-bottom: 55px;

    *:last-child {
        margin-bottom: 0;
    }

    h2 {
        font-size: 20px;
        line-height: 30px;
        font-style: italic;
        margin: 25px 0 15px;

        &:first-child {
            margin-top: 0;
        }
    }

    p {
        font-size: 14px;
        line-height: 20px;
        margin: 0 0 10px 0;

        &:last-child {
            margin-bottom: 0;
        }
    }

    ul {
        &.practices-description__ul-numbers {
            li {
                padding-left: 75px;
            }
        }

        margin-top: 30px;
        margin-bottom: 55px;

        li {
            padding-top: 8px;
            padding-left: 110px;
            position: relative;
            font-size: 14px;
            line-height: 20px;
            margin-bottom: 28px;

            &:last-child {
                margin-bottom: 0;
            }
        }

        .practices-description__marker-large,
        .practices-description__marker-num {
            width: 70px;
            height: 35px;
            left: 30px;
            top: 0;
            position: absolute;
            display: block;
            border-radius: 6px;
            background: #3d3d6d;
            line-height: 35px;
            text-align: center;
            font-size: 16px;
            font-weight: bold;
            font-family: $f-PTSans;
        }

        .practices-description__marker-num {
            width: 35px;
            height: 35px;
        }
    }
}

.h-section-practices__form-title {
    font-size: 20px;
    line-height: 30px;
    font-style: italic;
    margin: 0 0 25px 0;
    color: #fff;
}

.h-section-practices__form {
    max-width: 460px;

    .h-section-practices__form-c {
        margin-bottom: 110px;
    }
}

@media only screen and (max-width : 1365px) {
    .h-section-practices__col-r-in {
        height: calc(100% + 20px);
        padding-right: 100px;
        padding-top: 15px;
    }

    .h-section-practices__col-r {
        &:after {
            display: none;
        }
    }

    .practices-description {
        margin-bottom: 52px;

        h2 {
            margin: 25px 0 10px;
        }

        p {
            font-size: 12px;
            line-height: 20px;
            margin-bottom: 0;
        }

        ul {
            margin-bottom: 50px;
            margin-top: 27px;

            &.practices-description__ul-numbers {
                li {
                    padding-left: 45px;
                }
            }

            li {
                padding-top: 8px;
                padding-left: 80px;
                font-size: 12px;
                line-height: 20px;
                margin-bottom: 25px;

                &:last-child {
                    margin-bottom: 0;
                }

                br {
                    display: none;
                }
            }

            .practices-description__marker-large,
            .practices-description__marker-num {
                left: 0;
            }
        }
    }

    .h-section-practices__form-title {
        margin: 0 0 27px 0;
    }

    .h-section-practices__form {
        max-width: 100%;
    }

    .h-section__title {
        &.h-section__title_practices {
            font-size: 55px;
            line-height: 60px;
            margin-bottom: 50px;

            br {
                display: none;
            }
        }
    }

    .h-section__info {
        &.h-section__info_practices {
            margin-top: 0;
            padding-top: 40px;
            height: 100%;
            display: flex;
            flex-direction: column;
        }
    }

    .h-section__video-block {
        margin: 0;
        max-width: 100%;
        flex: 1;
        height: auto;
        display: flex;
        flex-direction: column;
        // overflow: hidden;
    }

    .video-block__toggler {
        display: block;
        width: 100%;
        outline: none;
        background: none;
        border: none;
        border-radius: 0;
        margin: 0;
        padding: 0 25px 10px 0;
        cursor: pointer;
        color: #fff;
        font-size: 14px;
        font-style: italic;
        position: relative;
        border-bottom: 1px solid #dddddd;
        text-align: left;
        font-family: inherit;

        &:before {
            position: absolute;
            display: block;
            content: "";
            width: 8px;
            height: 8px;
            border: 2px solid #fff;
            border-top: none;
            border-right: none;
            right: 8px;
            top: 2px;
            transform: rotate(-45deg);
            transition: all .3s;
        }

        &.active {
            &:before {
                transform: rotate(135deg);
                top: 8px;
            }
        }
    }

    .video-block__list {
        flex-direction: column;
        padding-top: 25px;
        padding-bottom: 40px;
        margin-bottom: 90px;
        border-bottom: 1px solid #dddddd;

        &:after {
            display: none;
        }
    }

    .video-block__item {
        margin-bottom: 25px;
        margin-right: 0;
        width: 84%;

        &:last-child {
            margin-bottom: 0;
        }
    }

    .video-block__title {
        line-height: 15px;
        max-height: 31px;
    }

    .h-section-practices__col-l-in,
    .h-section-practices__col-l-full-row,
    .h-section__info_practices {
        height: 100%;
    }

    .video-block__list-c {
        height: 1;
        overflow: auto;
        margin-right: -50px;
        padding-right: 50px;
        padding-bottom: 90px;
        flex: 0;
        padding-bottom: 0;
        transition: all .5s;

        &.active {
            padding-bottom: 90px;
            flex: auto;
        }

        &:after {
            display: none;
        }
    }
}

@media only screen and (max-width : 767px) {
    .h-section-practices__col-l,
    .h-section-practices__col-r {
        flex: 0 0 auto;
        max-width: 100%;

        &.h-100p {
            height: auto;
        }
    }

    .h-section__info {
        &.h-section__info_practices {
            padding-top: 35px;
            height: auto;
        }
    }

    .h-section__title {
        &.h-section__title_practices {
            font-size: 40px;
            line-height: 45px;
            max-width: 75%;
        }
    }

    .h-section-practices__col-l-in, 
    .h-section-practices__col-l-full-row, 
    .h-section__info_practices {
        height: auto;
    }

    .h-section-practices__col-r-in {
        height: auto;
        padding-top: 25px;
        overflow: visible;
        padding-right: 0;
        margin-right: 0;
    }

    .h-section__video-block {
        overflow: visible;
        height: auto;
        flex: 0 0 auto;
    }

    .video-block__item {
        width: 100%;
    }

    .video-block__list-c {
        padding-bottom: 0;
        margin-right: 0;
        padding-right: 0;
        max-height: 0;
        overflow: hidden;
        height: auto;

        &.active {
            max-height: 4000px;
            padding-bottom: 0;
        }
    }

    .practices-description {
        h2 {
            margin: 25px 0 5px;
        }
    }

    .h-section-practices__col-wrap {
        overflow-x: hidden;
        overflow-y: auto;
        height: calc(100vh - 120px);
    }
}