form {
    margin: 0;
}

.body-overflow {
    position: fixed !important;
    overflow: hidden !important;
}

.h-section {
    position: relative;
    background: $c-darkblue;
    padding: 65px 53px 35px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    height: 100vh;

    &.h-section-main,
    &.h-section-askus {
        &:before {
            display: block;
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            height: 205px;
            background: linear-gradient(to bottom, rgba(black, 0.5) 0%, rgba(black, 0) 100%);
            z-index: 1;
        }
    }

    &.h-section-team {
        background: #fff;
    }

    &.h-section-location {
        background: #fff;

        &:before {
            display: block;
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            height: 205px;
            background: linear-gradient(to bottom, rgba(#fff, 1) 0%, rgba(#fff, 0) 100%);
            z-index: 1;
        }

        &:after {
            display: block;
            content: "";
            position: absolute;
            left: 0;
            right: auto;
            width: 45%;
            top: 0;
            bottom: 0;
            background: linear-gradient(to right, rgba(#fff, 1) 25%, rgba(#fff, 0) 100%);
            pointer-events: none;
        }
    }

    &_solo-section {
        transform: translate3d(0px, 0px, 0px);
    }
}

.sections-structure {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 25px 53px 35px;
    z-index: 1;
    pointer-events: none;

    &_ui-white {
        .sections-structure__in {
            border-color: rgba(#fff, 0.3);
        }

        .fake-nav__in {

            &:before,
            &:after {
                background: rgba(#fff, 0.3);
            }
        }
    }

    &_ui-gray {
        .sections-structure__in {
            border-color: #dddddd;
        }

        .fake-nav__in {

            &:before,
            &:after {
                background: #dddddd;
            }
        }

        .svg-icon-logo {
            fill: $c-darkblue;
        }

        .main-nav__link {
            @include states {
                color: #999999;
            }

            &.active {
                color: $c-darkblue;
            }

            &:hover {
                border-color: #dddddd;
            }
        }

        .header__line:before {
            background: #dddddd;
        }

        .lang__current {
            &:before {
                border-color: $c-darkblue;
            }

            &:hover {
                .lang__current-text {
                    border-color: $c-darkblue;
                }
            }
        }

        .lang__current-text {
            color: $c-darkblue;
        }

        .lang__link {
            @include states {
                color: #dddddd;
            }

            &:hover {
                border-color: #dddddd;
            }

            &.active {
                color: $c-darkblue;
            }
        }

        .sections-structure__left-border-top,
        .sections-structure__left-border-bot {
            background: #dddddd;
        }
    }
}

.sections-structure__in {
    position: relative;
    height: 100%;
    border-bottom: 1px solid rgba(#fff, 0.3);
    transition: all .3s;
}

.sections-structure__left-border {
    position: absolute;
    top: 45px;
    left: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    width: 1px;
    overflow: hidden;
}

.sections-structure__left-border-top {
    flex: 0 0 auto;
    height: 100px;
    width: 1px;
    background: rgba(#fff, 0.3);
    transition: all .3s;
}

.sections-structure__left-border-mid {
    flex: 0 0 auto;
    height: 100px;
    width: 1px;
    transition: all .3s;
    margin: 10px 0;

    &_added-margin {
        margin-bottom: 25px;
    }
}

.sections-structure__left-border-bot {
    flex: 1;
    height: auto;
    width: 1px;
    background: rgba(#fff, 0.3);
}

.sections-structure__fake-nav {
    position: absolute;
    top: 45px;
    bottom: 0;
    display: flex;
    flex-direction: column;
    right: 0;
    width: 1px;
    justify-content: center;
    overflow: hidden;
}

.sections-structure__bot {
    display: none;
}

.fake-nav__in {
    height: 0;
    flex: 0 0 auto;
    width: 100%;
    margin-top: 0;
    position: relative;

    &:before,
    &:after {
        position: absolute;
        content: "";
        display: block;
        width: 100%;
        height: 2000px;
        background: rgba(#fff, 0.3);
        right: 0;
    }

    &:before {
        bottom: 100%;
        margin-bottom: 0;
    }

    &:after {
        top: 100%;
        margin-top: 0;
    }

    &.active {
        margin-top: -25px;

        &:before {
            margin-bottom: 20px;
        }
    
        &:after {
            margin-top: 20px;
        }
    }
}

.h-section__bg-particles {
    position: absolute;
    left: 0;
    bottom: 0;
    pointer-events: none;
    z-index: 1;
}

.h-section__bg-img {
    position: absolute;
    left: 0;
    bottom: 0;
}

.js--custom-dropdown {
    z-index: 10;
    display: none;

    &.active {
        display: block;
    }
}

@media only screen and (max-width : 1365px) {

    #fp-nav,
    .sections-structure__fake-nav,
    .sections-structure__left-border,
    .h-section__bg-particles,
    .row-spacer {
        display: none;
    }

    .sections-structure {
        padding: 0 20px 20px;

        &_ui-gray {
            .sections-structure__bot-line {
                background: #dddddd;
            }

            .svg-icon-logo {
                fill: #fff;
            }

            .header {
                border-color: transparent;

                &:before {
                    opacity: 1;
                }
            }
        }

        &[data-current=main] {
            .sections-structure__scroll-ico-c {
                display: block;
            }
        }

        &[data-current=Practices] {
            .sections-structure__scroll-ico-c {
                display: block;
            }
        }

        &[data-current^=about] {
            .sections-structure__scroll-ico-c {
                display: block;

                .sections-structure__scroll-ico {
                    border: 1px solid $c-darkblue;
                    border-top: none;
                    border-right: none;
                }
            }
        }

        &.menu-is-opened {
            .sections-structure__scroll-ico-c {
                display: none;
            }

            &.sections-structure_ui-gray,
            &.sections-structure_ui-white {
                .sections-structure__bot-line {
                    background: rgba(255, 255, 255, 0.3);
                }
    
                .header {
                    border-color: rgba(255, 255, 255, 0.3);
    
                    &:before {
                        display: none;
                    }
                }

                .lang__link {
                    @include states {
                        color: rgba(255, 255, 255, 0.6);
                    }

                    &:hover {
                        border-color: rgba(255, 255, 255, 0.6);
                    }

                    &.active {
                        color: #fff;
                    }
                }
            }

            &:before,
            &:after {
                opacity: 0;
                pointer-events: none;
            }
        }
    }

    .sections-structure__in {
        border-bottom: none;
    }

    .sections-structure__bot {
        pointer-events: all;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        z-index: 2;
    }

    .sections-structure__bot-line {
        height: 1px;
        flex: 1;
        margin-right: 20px;
        background: rgba(#fff, 0.3);
    }

    .sections-structure__scroll-ico {
        display: block;
        position: absolute;
        left: 50%;
        width: 14px;
        height: 14px;
        border: 1px solid #ffffff;
        border-top: none;
        border-right: none;
        transform: translateX(-50%) rotate(-45deg);
        top: -35px;
    }

    .sections-structure__scroll-ico-c {
        display: none;
        position: absolute;
        left: 0;
        right: 0;
        pointer-events: none;
        animation: bounce 1.5s infinite;
    }

    .h-section {
        &.h-section-practices {
           &:before {
                position: absolute;
                display: block;
                content: "";
                width: 100%;
                height: 135px;
                bottom: 0;
                left: 0;
                right: 0;
                background: linear-gradient(to top, #0d0d49 45%, rgba(#0d0d49, 0) 100%);
                z-index: 2;
                pointer-events: none;
                transition: .3s;
           } 
        }

        &.h-section-about {
            &:before {
                 position: absolute;
                 display: block;
                 content: "";
                 width: 100%;
                 height: 135px;
                 bottom: 0;
                 left: 0;
                 right: 0;
                 background: linear-gradient(to top, #fff 45%, rgba(#fff, 0) 100%);
                 z-index: 2;
                 pointer-events: none;
                 transition: .3s;
            } 
         }
    }
}

@media only screen and (max-width : 767px) {
    .h-section {
        &.h-section-main {
            min-height: 100vh;

            .sections-structure__scroll-ico-c {
                display: block;
            }

            .sections-structure__scroll-ico {
                top: -95px;
            }
        }

        &.h-section-location {
            min-height: 100vh;
        }

        &.h-section-about {
            &:before {
                display: none;
            }
        }
    }

    .sections-structure {
        padding: 0 20px 20px;

        &[data-current=main] {
            .sections-structure__scroll-ico-c {
                display: none;
            }
        }

        &[data-current^=about] {
            &:before {
                position: absolute;
                display: block;
                content: "";
                width: 100%;
                height: 135px;
                bottom: 0;
                left: 0;
                right: 0;
                background: linear-gradient(to top, #fff 45%, rgba(#fff, 0) 100%);
                z-index: 2;
                pointer-events: none;
                transition: .3s;
            }

            .sections-structure__scroll-ico-c {
                .sections-structure__scroll-ico {
                    top: -65px;
                }
            }
        }
    }
}