//typography helpers
.text {

	&-normal,
	&-regular,
	&-fw_normal,
	&-fw_regular,
	&-fw_400 {
		font-weight: 400;
	}

	&-bold,
	&-fw_bold,
	&-fw_600 {
		font-weight: 600;
	}

	&-fw_700 {
		font-weight: 700;
	}

	&-exbld,
	&-fw-exbld,
	&-fw-800 {
		font-weight: 800;
	}

	&-center {
		text-align: center;
	}

	&-uppercase {
		text-transform: uppercase;
	}

	&-crossed {
		text-decoration: line-through;
	}

	&-dashed {
		text-decoration: underline dashed;
	}
}

