//common helpers
.clearfix::after {
	display: block;
	clear: both;
	content: "";
}

.pos-rel {
	position: relative !important;
}

.pos-abs {
	position: absolute !important;
}

.rounded {
	border-radius: 50%;
}

.img-fluid {
	max-width: 100%;
	height: auto;
}

.float-right {
	float: right;
}

.float-left {
	float: left;
}


.show {
	display: block !important;
}

.hide {
	display: none !important;
}

.v-hidden {
	visibility: hidden !important;
}

.v-visible {
	visibility: visible !important;
}

.overflow-hidden {
	overflow: hidden;
}

.overflow-visible {
	overflow: visible;
}

.overflow-auto {
	overflow: auto;
}


.bg-transparent,
.bg-opacity {
	background-color: transparent;
}

.rotate-0 {
	transform: rotate(0deg);
}

.rotate-90 {
	transform: rotate(90deg);
}

.rotate-180 {
	transform: rotate(180deg);
}

.rotate-270 {
	transform: rotate(270deg);
}

.text-hide {
    font: 0/0 a;
    color: transparent;
    text-shadow: none;
    background-color: transparent;
    border: 0;
}

.scrollable {
	overflow-x: hidden;
	overflow-y: auto;
}

.white-space-nowrap {
	white-space: nowrap;
}