.h-section__title {
    font-size: 125px;
    line-height: 130px;
    color: #fff;
    position: relative;
    margin-bottom: 25px;

    &:last-child {
        margin-bottom: 0;
    }

    &_color-blue {
        color: $c-darkblue;
    }
}

.video-bg {
    position: absolute;
    height: 100%;
    width: 100%;
    position: absolute;
    padding: 0;
    top: 0;
    left: 0;
    overflow: hidden;

    &:before {
        position: absolute;
        display: block;
        content: "";
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba(#000, 0.6);
        z-index: 1;
    }
}

.video-bg {
    pointer-events: none;

    iframe {
        position: absolute;
        width: 100%;
        height: 100%;
    }

    video {
        position: absolute;
        width: auto;
        height: auto;
        min-width: 100%;
        min-height: 100%;
        top: 0;
        left: 0;
    }
}

.h-section__info {
    margin-bottom: 55px;

    &:last-child {
        margin-bottom: 0;
    }
}

.h-section__desc {
    font-size: 14px;
    line-height: 20px;
    color: #666666;
    margin-bottom: 55px;
    text-align: justify;

    p {
        margin: 0 0 20px 0;
        
        &:last-child {
            margin-bottom: 0;
        }
    }

    &:last-child {
        margin-bottom: 0;
    }

    &_color-white {
        color: #fff;
    }
}

.h-section__logos-list {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-wrap: wrap;
}

.h-section__logos-item {
    margin-right: 15px;
    flex: 0 0 auto;

    &:last-child {
        margin-right: 0;
    }
}

.h-section__logos-img {
    max-width: 100%;
    height: auto;
    opacity: 0.5;
}

.h-section__team-slider-c {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    height: 100%;
    padding-top: 9vh;
}

.team-slider {
    .team-slider__item.slick-slide {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    img.team-slider__flag {
        display: none;
    }

    .slick-active {
        +.slick-active.active-item {
            transform: translateX(-60px);
        }
    }
}

.team-slider__img {
    max-width: 100%;
    width: 100%;
    height: auto;
}

.team-slider__img-container {
    border-radius: 50%;
    height: 130px;
    width: 130px;
    margin-bottom: 25px;
    overflow: hidden;
    filter: grayscale(100);
    opacity: 0.6;
    flex: 0 0 auto;
    transition: .7s;
}

.team-slider__name-c {
    position: relative;
    flex: 0 0 auto;
}

.team-slider__name {
    font-size: 14px;
    font-style: italic;
    color: #777777;
    border-bottom: 1px dashed #777777;
    padding-bottom: 2px;
}

.team-slider__item {
    outline: none;
    cursor: pointer;
    padding-top: 102px;
    transition: all 0.5s;

    &.active-item {
        padding-top: 30px;
        cursor: default;
        transform: translateX(60px);

        .team-slider__img-container {
            width: 240px;
            height: 240px;
            filter: none;
            opacity: 1;
        }

        .team-slider__name {
            font-size: 20px;
            color: #000066;
            border-bottom: transparent;

            &:before {
                display: inline-block;
                content: "";
                margin-right: 5px;
            }
        }

        .team-slider__name-c {
            padding: 0 25px;
        }

        .team-slider__info-c {
            height: auto;
            flex: 1;
        }

        img.team-slider__flag {
            display: block;
        }
    }
}

.team-slider__flag {
    position: absolute;
    top: -20px;
    right: 0;
}

.team-slider__desc {
    text-align: center;
    font-size: 14px;
    line-height: 20px;
    font-style: italic;
    color: #000000;
    margin-bottom: 20px;
    max-width: 100%;

    p {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.team-slider__tel {
    font-size: 14px;
    line-height: 25px;
    padding-left: 33px;
    position: relative;
    text-align: center;
    color: #000066;
}

.team-slider__mail {
    position: relative;
    padding-left: 35px;
    font-size: 14px;
    line-height: 25px;
    color: #000066;
    text-align: center;
}

.team-slider__tel-ico,
.team-slider__mail-ico {
    fill: #000066;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

.team-slider__info-c {
    overflow: hidden;
    flex: 0;
    height: 0;
    transition: all .5s;
}

.team-slider__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 340px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}

.h-section__services {
    display: flex;
    height: 100%;
    align-items: flex-start;
}

.services-slider {
    display: none;
}

.services__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
}

.services__item {
    flex: 0 0 auto;
    width: 25%;
    padding: 0 10px;
    margin-bottom: 20px;
}

.services__card {
    display: block;
    position: relative;
    background: linear-gradient(90deg, rgb(52, 52, 106) 0%, rgb(26, 26, 85) 100%);
    border-radius: 6px;

    &:before {
        display: block;
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: linear-gradient(to top, rgb(52, 52, 106) 0%, rgb(26, 26, 85) 100%);
        border-radius: 6px;
        pointer-events: none;
        transition: all .3s;
    }

    &:after {
        display: block;
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: linear-gradient(60deg, rgb(0, 0, 102) 0%, rgb(195, 225, 236) 100%);
        border-radius: 6px;
        pointer-events: none;
        transition: all .3s;
        opacity: 0;
    }

    @media only screen and (min-width : 1366px) {
        &:hover {

            &:before,
            &:after {
                top: -5px;
                bottom: -5px;
                left: -5px;
                right: -5px;
                opacity: 1;
            }

            .services__img-front {
                opacity: 0;
            }

            .services__img-back {
                opacity: 1;
            }

            .services__img-container {
                transform: translateY(-20px) scale(1.4);
                transform: translateY(-20px) matrix(1.4,0,0,1.4,0,0);
            }

            .services__title {
                color: #fff;
                transform: translateY(-40px);
                font-size: 18px;
                text-shadow: 0px 3px 6px rgba(23, 23, 119, 0.42);
            }

            .services__subinfo {
                transform: translateY(0);
            }
        }
    }
}

.services__card-in {
    min-height: 210px;
    height: 15.5vw;
    display: flex;
    flex-direction: column;
    padding: 65px 0 20px;
    position: relative;
    z-index: 1;
}

.services__subinfo-c {
    overflow: hidden;
    height: 35px;
    position: absolute;
    bottom: -5px;
    left: -5px;
    right: -5px;
    height: -5px;
    border-radius: 5px 5px 0 0;
}

.services__subinfo {
    transform: translateY(100%);
    line-height: 35px;
    text-align: center;
    font-size: 12px;
    color: #8c90c7;
    font-style: italic;
    transition: all .3s;
    background: rgba(#000, 0.1);
}

.services__img-container {
    width: 80px;
    height: 80px;
    position: relative;
    margin: 0 auto auto;
    flex: 0 0 auto;
    transition: all .3s;

    transform: scale(1.3);
    transform: matrix(1.3,0,0,1.3,0,0);
}

.services__img {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    max-width: 100%;
    max-height: 100%;
    margin: auto;
    transition: all .3s;
}

.services__img-front {
    opacity: 1;
}

.services__img-back {
    opacity: 0;
}

.services__title {
    margin-top: 5px;
    text-align: center;
    font-size: 16px;
    line-height: 20px;
    color: #9393bc;
    transition: all .3s;
}

.h-section-askus__col-wrap.scrollable {
    margin-right: -40px;
    padding-right: 30px;
}

.h-form-c {
    width: 100%;
    background: #fff;
    box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
    padding: 13px 20px 30px;
    border-radius: 5px;
}

.h-form__input-wrap {
    margin-bottom: 25px;
    position: relative;

    &_mb-60 {
        margin-bottom: 60px;
    }

    &_mb-20 {
        margin-bottom: 20px;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

.h-form__input {
    font-family: $f-PTSerif;
    height: 35px;
    width: 100%;
    display: block;
    border: none;
    outline: none;
    border-radius: 0;
    border-bottom: 1px solid #dddddd;
    padding: 0 25px 0 0;
    font-size: 18px;
    color: #000066;

    &::placeholder {
        font-size: 14px;
        font-style: italic;
        color: rgba(#666666, 0.6);
    }
}

.h-form__btn-clear-input {
    display: none;
    right: 0;
    position: absolute;
    border-radius: 0;
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
    width: 20px;
    height: 100%;
    top: 0;
    padding: 0;

    &:before,
    &:after {
        position: absolute;
        display: block;
        content: "";
        width: 11px;
        height: 1px;
        background: #999999;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(45deg);
    }

    &:after {
        transform: translate(-50%, -50%) rotate(-45deg);
    }
}

.has-value+.h-form__btn-clear-input {
    display: block;
}

.h-form__input-title {
    font-size: 14px;
    line-height: 20px;
    color: #666666;
    font-style: italic;
    margin-bottom: 5px;
}

.h-form__textarea {
    font-family: $f-PTSerif;
    height: 120px;
    width: 100%;
    display: block;
    outline: none;
    border-radius: 5px;
    border: 1px solid #dddddd;
    padding: 10px;
    font-size: 18px;
    color: #000066;
    resize: none;

    &::placeholder {
        font-size: 14px;
        font-style: italic;
        color: rgba(#666666, 0.6);
    }
}

.h-form__sbm-btn {
    position: relative;
    width: 130px;
    border-radius: 3px;
    padding: 0 10px 0 30px;
    text-align: left;
    line-height: 36px;
    height: 35px;
    outline: none;
    border: none;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
    background: $c-darkblue;
    transition: all .3s;

    &:hover,
    &:active {
        background: rgb(30, 30, 105);
        box-shadow: 0px 5px 8px 0px rgba(172, 197, 255, 0.004);
    }
}

.h-form__sbm-btn-ico {
    display: block;
    margin-left: 14px;
    fill: #fff;
    position: absolute;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
}

.h-section__map {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.h-form__select.custom-select {
    display: none;
}

.fake-select__current {
    position: relative;
    cursor: pointer;
    height: 35px;
    line-height: 35px;
    width: 100%;
    display: block;
    border: none;
    outline: none;
    border-radius: 0;
    border-bottom: 1px solid #dddddd;
    padding: 0 25px 0 0;
    font-size: 14px;
    color: #777777;
    font-style: italic;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;

    &.active {
        color: #000066;
        font-size: 18px;
        font-style: normal;
    }

    &:before {
        display: block;
        content: "";
        height: 10px;
        width: 10px;
        position: absolute;
        top: 50%;
        right: 4px;
        border: 1px solid #999999;
        border-top: none;
        border-right: none;
        transform: translateY(-50%) rotate(-45deg);
        margin-top: -2.5px;
    }
}

.fake-select__dropdown {
    background: #fff;
    position: absolute;
    top: 100%;
    margin-top: -35px;
    left: -10px;
    right: -10px;
    border-radius: 5px;
    box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.2);
    background: #fff;
    padding: 0 10px;
}

.fake-select__dropdown-tittle {
    font-weight: normal;
    font-style: italic;
    cursor: default;
    display: block;
    padding: 0px;
    color: #777777;
    height: 35px;
    border-bottom: 1px solid rgba(#dddddd, 0.3);
    line-height: 35px;
    font-size: 14px;
    position: relative;
    padding-right: 30px;

    &:before {
        display: block;
        content: "";
        height: 10px;
        width: 10px;
        position: absolute;
        top: 50%;
        right: 4px;
        border: 1px solid #999999;
        border-top: none;
        border-right: none;
        transform: translateY(-50%) scale(-1) rotate(-45deg);
        margin-top: 2.5px;
    }
}

.fake-select__options-list {
    padding: 10px 0;
}

.fake-select__options-value {
    cursor: pointer;

    padding: 7px 9px;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    font-style: italic;
    color: #adadad;
    transition: .3s;

    &:hover {
        color: #adadad;
        background: transparent;
        text-decoration: underline;
    }

    &.active {
        background: none;
        color: #0d0d49;
        text-decoration: none;
    }
}

.h-section-team__col-r {
    .h-section__logos {
        display: none;
    }
}

@media (min-aspect-ratio: 16/9) {
    .video-bg iframe {
        height: 300%;
        top: -100%;
    }
}

@media (max-aspect-ratio: 16/9) {
    .video-bg iframe {
        height: 150%;
        top: -25%;
        width: 300%;
        left: -100%
    }
}

@media only screen and (max-width : 1800px) {
    .h-section__title {
        font-size: 90px;
        line-height: 100px;
    }

    .h-section__team-slider-c {
        padding-top: 6vh;
    }

    .h-section__team-slider-c {
        width: 90%;
    }
}

@media only screen and (max-width : 1600px) {
    .services__card-in {
        padding: 45px 0 20px;
    }
}

@media only screen and (max-width : 1600px) and (min-width: 1366px) {
    .services__img-container {
        transform: none;
    }

    .services__card {
        &:hover {
            .services__img-container {
                transform: translateY(-20px) scale(1.1);
                transform: translateY(-20px) matrix(1.1,0,0,1.1,0,0);
            }
        }
    }
}

@media only screen and (max-width : 1365px) {
    .h-section {
        padding: 60px 20px 60px;
    }

    .h-section__title {
        font-size: 65px;
        line-height: 75px;
    }

    .h-section__desc {
        font-size: 12px;
        line-height: 18px;
    }

    .h-section-main__container {
        padding-top: 40px;

        .col-4 {
            flex: 0 0 100%;
            max-width: 100%;
        }
    }

    .h-section-services__container {
        padding-top: 35px;

        .h-section__title {
            br {
                display: none;
            }
        }
    }

    .h-section-services__col-wrap {
        flex-direction: column;
        flex-wrap: nowrap;
    }

    .h-section-services__col-l {
        flex: 0 0 auto;
        max-width: 100%;
        height: auto;

        &.h-100p {
            height: auto;
        }
    }

    .h-section-services__col-r {
        max-width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        min-height: 0;
        min-width: 0;
        flex: 1;
        flex: auto;

        &.h-100p {
            height: auto;
        }
    }

    .h-section__services {
        padding-bottom: 0;
        display: flex;
        flex: 1;
        flex: auto;
        min-width: 0;
        min-height: 0;
    }

    .services__subinfo-c {
        display: none;
    }

    .services__list {
        display: none;
    }

    .services-slider {
        margin-left: -10%;
        margin-right: -10%;
        margin-top: auto;
        margin-bottom: auto;
        display: block;
        height: auto;
        flex: 1;
        min-width: 0;
        min-height: 0;

        .slick-list {
            padding: 30px 0 !important;
        }

        .slick-track {
            display: flex;
        }

        .slick-dots {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            display: flex;
            flex-wrap: wrap;
            
            li {
                margin-right: 10px;
    
                &.slick-active {
                    button {
                        background: #d4e4eb;
                        box-shadow: 0 0px 10px 2px rgba(53, 62, 139, 0.4);
                    }
                }
    
                button {
                    outline: none;
                    border: none;
                    margin: 0;
                    padding: 0;
                    width: 6px;
                    height: 6px;
                    border-radius: 50%;
                    background: #565690;
                    display: block;
                    font-size: 0;
                    color: transparent;
                    cursor: pointer;
                    flex: 0 0 auto;
                }
    
                &:last-child {
                    margin-right: 0;
                }
            }
        }
    }

    .services__item {
        display: block;
        margin-bottom: 0;
        height: 32.6vw;
        padding: 0;
        transform: scale(0.6);
        transition: all .3s;

        &.slick-center {
            box-shadow: 0 0px 40px 0px rgba(53, 62, 139, 0.4);
            position: relative;
            z-index: 1;

            .services__card {
                &:before {
                    opacity: 0;
                }

                &:after {
                    opacity: 1;
                }
            }

            .services__img-front {
                opacity: 0;
            }

            .services__img-back {
                opacity: 1;
            }

            .services__title {
                color: #fff;
                text-shadow: 0px 3px 6px rgba(23, 23, 119, 0.42);
            }

            .services__card-in {
                padding-bottom: 40px;
            }
        }

        &.medium {
            .services__card-in {
                padding-bottom: 25px;
            }

            .services__title {
                font-size: 18px;
            }

            .services__img-container {
                transform: scale(1.3);
                transform: matrix(1.3,0,0,1.3,0,0);
            }
        }

        &.medium-l {
            transform-origin: right center;
            transform: translateX(-10%) scale(0.8);
        }

        &.medium-r {
            transform-origin: left center;
            transform: translateX(10%) scale(0.8);
        }

        &.low {
            .services__card-in {
                padding-bottom: 30px;
            }

            .services__title {
                font-size: 16px;
            }

            .services__img-container {
                transform: scale(1.15);
                transform: matrix(1.15,0,0,1.15,0,0);
            }
        }

        &.low-l {
            transform-origin: right center;
            transform: translateX(0) scale(0.6);
            opacity: 0.5;
        }

        &.low-r {
            transform-origin: left center;
            transform: translateX(0) scale(0.6);
            opacity: 0.5;
        }

        &.big {
            transform: scale(1);
            transform: matrix(1,0,0,1,0,0);
        }
    }

    .services__card {
        width: 100%;
    }

    .services__card-in {
        min-height: 100%;
        height: 100%;
        transition: all .3s;
        padding-top: 30%;
    }

    .services__img-container {
        transform: scale(1.5);
        transform: matrix(1.5,0,0,1.5,0,0);
    }

    .services__title {
        font-size: 20px;
        padding: 0 5px;
    }

    .h-section {
        &.h-section-location {
            &:before {
                height: 60%;
            }

            &:after {
                left: 0;
                right: 0;
                bottom: 0;
                top: auto;
                width: auto;
                height: 25%; 
                background: linear-gradient(to top, white 35%, rgba(255, 255, 255, 0) 100%);
                pointer-events: none;
            }
        }
    }

    .h-section-location__container {
        padding-top: 40px;

        .h-section__info {
            display: flex;
            flex-wrap: wrap;
            margin-left: -10px;
            margin-right: -10px;
        }

        .h-section__title {
            padding-left: 10px;
            padding-right: 10px;
            flex: 0 0 auto;
            width: 50%;
        }

        .h-section__desc {
            padding-left: 10px;
            padding-right: 10px;
            flex: 0 0 auto;
            width: 50%;
            padding-top: 20px;

            p {
                &:first-child {
                    br {
                        display: none;
                    }
                }
            }
        }
    }

    .h-section-location__col {
        flex: 0 0 100%;
        max-width: 100%;
    }

    .h-section-askus__container {
        padding-top: 20px;

        .h-section__info {
            padding-top: 85px;
        } 
    }

    .h-section-askus__col-wrap {
        height: auto;
        max-height: 100%;
        margin-top: auto;
        margin-bottom: auto;
        align-items: flex-start;
    }

    .h-section-askus__col-l,
    .h-section-askus__col-r {
        flex: 0 0 50%;
        max-width: 50%;
        margin: 0;
        height: auto;

        &.h-100p {
            height: auto;
        }
    }

    .h-form-c {
        padding-bottom: 20px;
    }

    .h-form__textarea {
        height: 80px;
    }

    .h-section-team__container {
        padding-top: 10px;

        .h-section__info {
            padding-top: 110px;
            margin-bottom: 40px;
        } 

        .row-wrap {
            height: 100%;
        }
    }

    .h-section-team__col-wrap {
        height: auto;
        max-height: 100%;
        margin-top: auto;
        margin-bottom: auto;
    }

    .h-section-team__col-l,
    .h-section-team__col-r {
        flex: 0 0 50%;
        max-width: 50%;
        margin: 0;
    }

    .h-section__team-slider-c {
        width: 100%;
        padding-top: 0;
        overflow: hidden;
    }

    .h-section__team-slider {
        overflow-x: hidden;
        overflow-y: auto;
        max-height: 100%;
        margin-right: -20px;
        padding-right: 20px;
        padding-top: 75px;
        position: relative;

        &:after {
            display: block;
            content: "_";
            height: 150px;
            width: 100%;
            background: none;
            font-size: 1px;
            color: transparent;
        }
    }

    .h-section-team__col-r-full-row {
        height: 100%;    
    }

    .h-section__logos-list {
        justify-content: flex-start;
    }

    .h-section__logos-item {
        margin-bottom: 15px;
    }

    .team-slider__item,
    .team-slider__item.active-item {
        padding-top: 0;
        transform: none;
        cursor: default;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 60px;

        .team-slider__img-container {
            width: 240px;
            height: 240px;
            filter: none;
            opacity: 1;
            margin-left: auto;
            margin-right: auto;
        }

        .team-slider__name {
            font-size: 20px;
            color: #000066;
            border-bottom: transparent;

            &:before {
                display: inline-block;
                content: "";
                margin-right: 5px;
            }
        }

        .team-slider__name-c {
            padding: 0 25px;
        }

        .team-slider__info-c {
            height: auto;
            flex: auto;
        }

        img.team-slider__flag {
            display: block;
        }
    }

    .team-slider__desc {
        font-size: 12px;
        line-height: 20px;
    }
    
    .team-slider__flag {
        top: -15px;
    }

    .h-section.h-section-team:after {
        position: absolute;
        display: block;
        content: "";
        left: 0;
        right: 0;
        bottom: 0;
        top: auto;
        width: auto;
        height: 25%;
        background: linear-gradient(to top, white 45%, rgba(255, 255, 255, 0) 100%);
        pointer-events: none;
        z-index: 1;
    }
}

@media only screen and (max-width : 767px) {
    .h-section-main__container {
        .col-wrap {
            height: auto;
        }
    }

    .h-section__title {
        font-size: 40px;
        line-height: 45px;
    }

    .h-section-services__container {
        padding-top: 43px;

        .h-section__title {
            br {
                display: block;
            }
        }
    }

    .h-section-services__col-l {
        margin-bottom: 10px;
    }

    .h-section-services__col-r {
        padding-bottom: 30px;
    }

    .h-section__services,
    .h-section-services__col-wrap {
        height: auto;
    }

    .services-slider {
        .slick-list {
            padding: 35px 26vw 25px !important;
        }
    } 

    .services__item {
        height: 78vw;

        &.medium {
            .services__card-in {
                padding-bottom: 30px;
            }

            .services__title {
                font-size: 16px;
            }

            .services__img-container {
                transform: scale(1.15);
                transform: matrix(1.15,0,0,1.15,0,0);
            }
        }

        &.medium-l {
            transform-origin: right center;
            transform: translateX(-10%) scale(0.7);
            opacity: 1;
        }

        &.medium-r {
            transform-origin: left center;
            transform: translateX(10%) scale(0.7);
            opacity: 1;
        }
    }
    
    .h-section {
        &.h-section-location {
            &:before {
                height: 60%;
            }

            &:after {
                left: 0;
                right: 0;
                bottom: 0;
                top: auto;
                width: auto;
                height: 25%; 
                background: linear-gradient(to top, white 35%, rgba(255, 255, 255, 0) 100%);
                pointer-events: none;
            }
        }
    }

    .h-section-location__container {
        .h-section__title {
            width: 100%;
            margin-bottom: 10px;
        }

        .h-section__desc {
            width: 100%;
            padding-top: 0;

            p {
                &:first-child {
                    br {
                        display: block;
                    }
                }
            }
        }
    }

    .h-section__map {
        background-position: bottom center;
    }

    .h-section-askus__col-wrap.scrollable,
    .h-section-askus__col-wrap {
        padding-right: 0;
        margin-right: -10px;
        max-height: none;
    }

    .h-section-askus__container {
        padding-top: 0;
        padding-bottom: 40px;

        .h-section__info {
            padding-top: 40px;
        } 
    }

    .h-section-askus__col-l,
    .h-section-askus__col-r {
        flex: 0 0 100%;
        max-width: 100%;
        max-height: none;
        margin: 0;

        &.h-100p {
            height: auto;
        }
    }

    .h-section-askus__col-l {
        margin-bottom: 30px;
    }

    .h-form__textarea {
        height: 120px;
    }

    .h-section-team__col-wrap {
        flex-direction: column;
        flex-wrap: nowrap;
        margin-top: 0;
        margin-bottom: 0;
        max-height: none;

        .row-wrap {
            height: auto;
        }
    }

    .h-section-team__col-r-full-row,
    .h-section__team-slider-c {
        height: auto;
    }

    .h-section-team__col-l {
        .h-section__logos {
            display: none;
        }
    }

    .h-section-team__col-r {
        .h-section__logos {
            display: block;
        }
    }

    .h-section-team__container {
        padding-top: 0;

        .h-section__info {
            padding-top: 35px;
        } 

        .h-section__title {
            margin-bottom: 15px;
        }
    }

    .h-section-team__col-l,
    .h-section-team__col-r {
        flex: 0 0 auto;
        max-width: 100%;
        margin: 0;

        &.h-100p {
            height: auto;
        }
    }

    .h-section.h-section-team:after {
        display: none;
    }

    .h-section__team-slider {
        max-height: none;
        margin-right: 0;
        padding-right: 0;
        padding-top: 0;
        margin-bottom: 65px;

        &:after {
            display: none;
        }
    }

    .team-slider__item, 
    .team-slider__item.active-item {
        &:last-child {
            margin-bottom: 0;
            padding-bottom: 0;
        }
    }

    .h-section__logos-list {
        align-items: center;
        justify-content: center;
    }

    .video-bg {
        iframe {
            width: 320%;
        }
    }
}