.svg-icon-logo_color-white {
    fill: #fff;
}

.header {
    flex: 0 0 auto;
    display: flex;
    align-items: flex-end;
    position: relative;
    z-index: 5;
    margin-bottom: 20px;
    pointer-events: all;
}

.header-l,
.header-r {
    display: flex;
    align-items: flex-end;
}

.header-logo {
    display: block;
    position: relative;
    flex: 0 0 auto;

    .svg-icon-logo {
        transition: all .5s;
    }
}

.header__nav {
    flex: 0 0 auto;
    padding-bottom: 2px;
}

.main-nav__list {
    display: flex;
    align-items: flex-end;
}

.main-nav__item {
    margin-right: 28px;
    position: relative;

    &:last-child {
        margin-right: 0;
    }

    &:hover {
        .nav-dropdown {
            opacity: 1;
            visibility: visible;
            pointer-events: all;
        }
    }
}

.main-nav__link {
    display: block;
    font-size: 16px;
    font-family: $f-PTSans;
    transition: all .3s;
    border-bottom: 1px solid transparent;

    @include states {
        color: rgba(#fff, 0.6);
    }

    &:hover {
        border-color: rgba(#fff, 0.6);
    }

    &.active {
        color: #fff;
        pointer-events: none;
    }
}

.main-nav__dropdown {
    top: 100%;
    margin-top: 17px;
    left: 50%;
    transform: translateX(-50%);

    &:before {
        position: absolute;
        display: block;
        content: "";
        top: -17px;
        height: 17px;
        left: 0;
        right: 0;
    }
}

.nav-dropdown {
    position: absolute;
    display: block;
    width: 180px;
    background: #fff;
    border-radius: 5px;
    padding: 18px 25px;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: opacity 0.3s;
    box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.2);
}

.nav-dropdown__item {
    margin-bottom: 12px;

    &:last-child {
        margin-bottom: 0;
    }
}

.nav-dropdown__link {
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    display: block;
    transition: all .3s;

    @include states {
        color: #adadad;
    }

    &:hover {
        color: $c-darkblue;
    }
}

.header__line {
    flex: 1;
    height: auto;
    position: relative;
    margin-left: 30px;

    &:before {
        position: absolute;
        display: block;
        content: "";
        width: 100%;
        right: 0;
        left: 0;
        bottom: 10px;
        height: 1px;
        background: rgba(#fff, 0.3);
    }
}

.header__lang {
    margin-left: auto;
    flex: 0 0 auto;
    padding-bottom: 3px;
}

.lang {
    position: relative;

    &.active {
        .lang__current {
            &:before {
                transform: translateY(-10%) rotate(-45deg) scale(-1);
            }
        }
    }
}

.lang__current {
    position: relative;
    padding-left: 30px;
    cursor: pointer;
    padding-right: 25px;

    &:before {
        position: absolute;
        display: block;
        content: "";
        width: 8px;
        height: 8px;
        border: 1px solid #fff;
        border-top: none;
        border-right: none;
        right: 2px;
        top: 50%;
        border-radius: 1px;
        transform: translateY(-60%) rotate(-45deg);
    }

    &:hover {
        .lang__current-text {
            border-color: #fff;
        }
    }
}

.lang__current-text {
    font-size: 16px;
    font-family: $f-PTSans;
    border-bottom: 1px solid transparent;
    color: #fff;
    transition: all .3s;
}

.lang__dropdown {
    position: absolute;
    margin-top: 30px;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);

    &:before {
        position: absolute;
        display: block;
        content: "";
        top: -30px;
        height: 30px;
        left: 0;
        right: 0;
    }
}

.lang__item {
    margin-bottom: 11px;
    pointer-events: none;
    padding-left: 30px;
    padding-right: 25px;

    &:last-child {
        margin-bottom: 0;
    }
}

.lang__link {
    font-size: 16px;
    font-family: $f-PTSans;
    border-bottom: 1px solid transparent;
    pointer-events: all;

    @include states {
        color: rgba(#fff, 0.6);
    }

    transition: all .3s;

    &.active {
        pointer-events: none;
        color: #fff;
    }

    &:hover {
        border-color: rgba(#fff, 0.6);
    }
}

.xs-menu,
.menu-toggler {
    display: none;
}

@media only screen and (max-width : 1365px) {

    .header__nav,
    .header__line,
    .header__lang {
        display: none;
    }

    .header-l {
        flex: 1 0 auto;
        max-width: none;
        align-items: center;
    }

    .header-r {
        margin-left: auto;
        flex: 0 0 auto;
        max-width: none;
    }

    .header {
        margin-bottom: 0;
        align-items: center;
        height: 60px;
        border-bottom: 1px solid rgba(#ffffff, 0.3);

        &:before {
            position: absolute;
            left: -20px;
            right: -20px;
            top: 0;
            bottom: -1px;
            display: block;
            content: "";
            background: $c-darkblue;
            opacity: 0;
            transition: all 0.3s;
            box-shadow: 0 0 10px 2px rgba(13, 13, 73, 0.4);
        }
    }

    .lang__list {
        display: flex;
        justify-content: flex-end;
    }

    .lang__item {
        margin-bottom: 0;
        margin-right: 10px;
        padding: 0;

        &:last-child {
            margin-right: 0;
        }
    }

    .lang__link {
        border: none;
    }

    .header__menu-toggler {
        margin-left: auto;
    }

    .menu-toggler {
        outline: none;
        border: none;
        background: none;
        cursor: pointer;
        padding: 0;
        display: block;
        width: 20px;
        position: relative;
        padding-top: 8px;

        &.active {
            .menu-toggler__bar:nth-of-type(1) {
                background: transparent;
                transform: rotate(180deg);
            }

            .menu-toggler__bar:nth-of-type(2) {
                transform: rotate(45deg);
                top: 8px;
            }

            .menu-toggler__bar:nth-of-type(3) {
                transform: rotate(135deg);
                bottom: 7px;
            }
        }
    }

    .menu-toggler__bar {
        transition: all .3s ease-in-out;
        position: relative;
        background-color: #fff;
        display: block;
        width: 20px;
        height: 1px;
        margin-bottom: 7px;
        margin-top: -1px;

        &:last-child {
            margin-bottom: 0;
        }

        &:nth-child(1) {
            transition: .1s ease-in-out background-color;
        }

        &:nth-child(2) {
            position: absolute;
            top: 0;
        }

        &:nth-child(3) {
            position: absolute;
            bottom: -1px;
        }
    }

    .xs-menu {
        display: block;
        position: fixed;
        z-index: 1;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        transform: translateY(-105%);
        transition: 0.5s;
        display: flex;
        flex-direction: column;
        background: $c-darkblue;
        padding: 70px 20px 55px;

        &.active {
            transform: translateY(0);
        }
    }

    .xs-menu__in {
        flex: 1;
        overflow-y: auto;
        pointer-events: all;
        display: flex;
        flex-direction: column;
    }

    .xs-menu__nav-list {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: auto;
        margin-bottom: auto;
    }

    .xs-menu__nav-item {
        margin-bottom: 30px;
        text-align: center;

        &:last-child {
            margin-bottom: 0;
        }
    }

    .xs-menu__nav-link {
        font-family: $f-PTSans;
        font-size: 28px;
        text-align: center;
        border-bottom: 2px solid transparent;

        @include states {
            color: rgba(#fff, 0.6);
        }

        &_has_adds {
            border-color: rgba(#fff, 0.6);
        }

        &.active {
            @include states {
                color: #fff;
            }

            &_has_adds {
                border-color: #fff;
            }
        }
    }

    .xs-menu__tabs {
        position: relative;
        height: 100%;
    }

    .xs-menu__tab {
        display: none;
        height: 100%;

        &.active {
            display: block;
        }
    }

    .xs-menu__tab-main-in {
        display: flex;
        height: 100%;
        justify-content: center;
    }

    .xs-menu__tab-back-btn {
        margin-top: 5px;
        font-size: 18px;
        font-family: $f-PTSans;
        padding-left: 18px;
        display: inline-block;
        position: relative;

        @include states {
            color: #fff;
        }

        margin-bottom: 30px;

        &:before {
            position: absolute;
            display: block;
            content: "";
            left: 3px;
            width: 9px;
            height: 9px;
            border: 1px solid #ffffff;
            border-top: none;
            border-right: none;
            top: 50%;
            transform: translateY(-50%) rotate(45deg);
        }
    }

    .xs-menu__submenu-list {
        padding-left: 40px;
    }

    .xs-menu__submenu-item {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    .xs-menu__submenu-link {
        font-size: 20px;
        font-family: $f-PTSans;

        @include states {
            color: rgba(#fff, 0.6);
        }
    }
}