* {
	box-sizing: border-box;
}

a {
	text-decoration: none;
}

html {
	max-width: 100vw;
	overflow-x: hidden;
	min-height: 100%;
}

body {
	overflow-x: hidden;
	position: relative;
}

.module-container {
	position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
	flex-direction: column;
	flex: 1;
	overflow: hidden;
	z-index: 1;
}

.row-default {
	margin-left: -10px;
	margin-right: -10px;
	display: flex;
	flex-wrap: wrap;
	flex: 1 0 auto;
	min-width: 0;
}

.col-wrap {
	margin-left: -10px;
	margin-right: -10px;
	display: flex;
	flex-wrap: wrap;
	height: 100%;
}

.row-wrap {
	display: flex;
	flex-direction: column;
	margin-top: -10px;
	margin-bottom: -10px;
	height: calc(100% + 20px);
}

@include make-grid-columns(8, 20px);

@include make-grid-rows(7, 20px);


@media only screen and (max-width : 1365px) {
	.module-container {
		flex-wrap: nowrap;
	}

	.row-1, .row-2, .row-3, .row-4, .row-5, .row-6, .row-7, .row {
        height: auto;
        flex: 0 0 auto;
        max-height: none;
        padding-top: 0;
        padding-bottom: 0;
    }

    .row-wrap {
        height: auto;
        margin-bottom: 0;
        margin-top: 0;
    }
}

@media only screen and (max-width : 767px) {
	.module-container {
		width: calc(100% + 40px);
		margin-left: -20px;
		margin-right: -20px;
		padding-left: 20px;
		padding-right: 20px;
		height: auto;
		flex: 0 0 auto;
	}
}