@mixin size($width, $height:$width) {
	width: $width;
	height: $height;
}

@mixin pv($pt, $pb:$pt) {
	padding-top: $pt;
	padding-bottom: $pb;
}

@mixin ph($pl, $pr:$pl) {
	padding-left: $pl;
	padding-right: $pr;
}


@mixin mv($mt, $mb:$mt) {
	margin-top: $mt;
	margin-bottom: $mb;
}

@mixin mh($ml, $mr:$ml) {
	margin-right: $mr;
	margin-left: $ml;
}

@mixin pseudo($display: block, $pos: absolute, $content: '') {
	content: $content;
	display: $display;
	position: $pos;
}

@mixin input-placeholder {
	&.placeholder {
		@content;
	}

	&:-moz-placeholder {
		@content;
	}

	&::-moz-placeholder {
		@content;
	}

	&:-ms-input-placeholder {
		@content;
	}

	&::-webkit-input-placeholder {
		@content;
	}
}

@mixin desktop () {
	@media screen and (max-width: 1200px) {
		@content;
	}
}

@mixin laptop () {
	@media screen and (max-width: 992px) {
		@content;
	}
}

@mixin tablets () {
	@media screen and (max-width: 768px) {
		@content;
	}
}

@mixin phones () {
	@media screen and (max-width: 576px) {
		@content;
	}
}

@mixin linksHover() {
	transition: all .3s ease-in-out;
	opacity: 1;

	&:hover {
		opacity: 0.6;
	}

	&:active {
		opacity: 1;
	}
}

@mixin states {
	@content;

	&:hover, &:active, &:focus {
		@content;
	}
}

@mixin clearfix {
	&:before,
	&:after {
		content: " ";
		display: table;
	}

	&:after {
		clear: both;
	}
}

@mixin make-grid-columns($columns, $gutter) {

    %grid-column {
        width: 100%;
        padding-right: $gutter / 2;
        padding-left: $gutter / 2;
    }

    @for $i from 1 through $columns {
        .col-#{$i} {
            @extend %grid-column;
        }
    }

    .col {
        @extend %grid-column;
    }

    .col {
        flex-basis: 0;
        flex-grow: 1;
        max-width: 100%;
    }

    @for $i from 1 through $columns {
        .col-#{$i} {
            @include make-col($i, $columns);
        }
    }

    @for $i from 0 through ($columns - 1) {
        @if not ($i==0) {

            .offset-col-#{$i} {
                @include make-col-offset($i, $columns);
            }
        }
    }

}

@mixin make-col($size, $columns) {
    flex: 0 0 percentage($size / $columns);
    max-width: percentage($size / $columns);
}

@mixin make-col-offset($size, $columns) {
    $num: $size / $columns;
    margin-left: if($num==0, 0, percentage($num));
}

@mixin make-grid-rows($rows, $gutter) {

    %grid-row {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        padding-top: $gutter / 2;
        padding-bottom: $gutter / 2;
    }

    @for $i from 1 through $rows {
        .row-#{$i} {
            @extend %grid-row;
        }
    }

    .row {
        @extend %grid-row;
    }

    .row {
        flex-basis: 0;
        flex-grow: 1;
        max-height: 100%;
    }

    @for $i from 1 through $rows {
        .row-#{$i} {
            @include make-row($i, $rows);
        }
    }

    @for $i from 0 through ($rows - 1) {
        @if not ($i==0) {

            .offset-row-#{$i} {
                @include make-row-offset($i, $rows);
            }
        }
    }

}

@mixin make-row($size, $rows) {
    flex: 0 0 100%;
    max-height: percentage($size / $rows);
}

@mixin make-row-offset($size, $rows) {
    $num: $size / $rows;
    margin-top: if($num==0, 0, percentage($num));
}