.video-block__list-c > .scroll-element,
.video-block__list-c > .scroll-element div
{
    border: none;
    margin: 0;
    padding: 0;
    position: absolute;
    z-index: 10;
}
 
.video-block__list-c > .scroll-element div {
    display: block;
    height: 100%;
    left: 0;
    top: 0;
    width: 100%;
}
 
.video-block__list-c > .scroll-element.scroll-x {
    bottom: 0;
    height: 3px;
    padding-top: 3px;
    padding-bottom: 3px;
    left: 0;
    right: 0;
}
 
.video-block__list-c > .scroll-element .scroll-element_outer {
    overflow: hidden;
}
 
.video-block__list-c > .scroll-element .scroll-element_track { 
    background-color: rgba(255,255,255,0.3); 
    height: 1px;
    top: auto;
    bottom: 3px;

    &:before {
        position: absolute;
        display: block;
        content: "";
        top: -5px;
        bottom: 0;
        width: 100%;
    }
}

.video-block__list-c > .scroll-element .scroll-bar { 
    background-color: #fff; 
    cursor: pointer;
    height: 3px;
    bottom: 3px;
    top: auto;

    &:before {
        position: absolute;
        display: block;
        content: "";
        top: -7px;
        bottom: -7px;
        width: 100%;
    }
}

.about-abilities__list-c > .scroll-element,
.about-abilities__list-c > .scroll-element div
{
    border: none;
    margin: 0;
    padding: 0;
    position: absolute;
    z-index: 10;
}
 
.about-abilities__list-c > .scroll-element div {
    display: block;
    height: 100%;
    left: 0;
    top: 0;
    width: 100%;
}
 
.about-abilities__list-c > .scroll-element.scroll-y {
    height: auto;
    bottom: 40px;
    right: 1px;
    top: 0;
    width: 3px;
}
 
.about-abilities__list-c > .scroll-element .scroll-element_outer {
    overflow: hidden;
}
 
.about-abilities__list-c > .scroll-element .scroll-element_track { 
    background-color: #dddddd; 
    height: 100%;
    top: 0;
    bottom: 0;
    width: 1px;
    right: 0;
    left: auto;

    &:before {
        position: absolute;
        display: block;
        content: "";
        top: 0;
        bottom: 0;
        left: -4px;
        right: -4px;
    }
}

.about-abilities__list-c > .scroll-element .scroll-bar { 
    background-color: $c-darkblue; 
    cursor: pointer;
    width: 3px;
    bottom: 3px;
    top: auto;

    &:before {
        position: absolute;
        display: block;
        content: "";
        top: -7px;
        bottom: -7px;
        width: 100%;
    }
}