// https://developer.mozilla.org/ru/docs/Web/CSS/font-weight
// 100	 () Thin (Hairline)
// 200	  () Extra Light (Ultra Light)
// 300	 Light
// 400	 Normal
// 500	 Medium
// 600	 Semi Bold (Demi Bold)
// 700	 Bold
// 800	  () Extra Bold (Ultra Bold)
// 900	 () Black (Heavy)


/* pt-sans-caption-regular - cyrillic_latin */
@font-face {
     font-family: 'PT Sans Caption';
     font-style: normal;
     font-weight: 400;
     src: url('../fonts/PT-Sans-Caption/pt-sans-caption-v11-cyrillic_latin-regular.eot');
     /* IE9 Compat Modes */
     src: local('PT Sans Caption'), local('PTSans-Caption'),
          url('../fonts/PT-Sans-Caption/pt-sans-caption-v11-cyrillic_latin-regular.eot?#iefix') format('embedded-opentype'),
          /* IE6-IE8 */
          url('../fonts/PT-Sans-Caption/pt-sans-caption-v11-cyrillic_latin-regular.woff2') format('woff2'),
          /* Super Modern Browsers */
          url('../fonts/PT-Sans-Caption/pt-sans-caption-v11-cyrillic_latin-regular.woff') format('woff'),
          /* Modern Browsers */
          url('../fonts/PT-Sans-Caption/pt-sans-caption-v11-cyrillic_latin-regular.ttf') format('truetype'),
          /* Safari, Android, iOS */
          url('../fonts/PT-Sans-Caption/pt-sans-caption-v11-cyrillic_latin-regular.svg#PTSansCaption') format('svg');
     /* Legacy iOS */
}

