.svg-icon {
	&-logo {
		@include size(190px, 25px);
	}

	&-tel {
		@include size(15px);
	}

	&-letter {
		@include size(19px, 12px);
	}

	&-form-btn-ico {
		@include size(18px);
	}

	&-about-list-marker {
		@include size(4px);
	}

	&-about-white-border-circle {
		@include size(344px, 90px)
	}

	&-about-white-border-circle-mobile {
		@include size(320px, 126px)
	}
}








