.h-section-about {
    background: #fff;
}

.h-section__title {
    &.h-section__title_about {
        font-size: 70px;
        line-height: 85px;
        margin-bottom: 15px;
    }
}

.h-section__desc {
    &.h-section__desc_about {
        max-width: 460px;
        text-align: left;
        margin-bottom: 30px;
    }
}

.h-section__info {
    &.h-section__info_about {
        margin-top: 25px;
        margin-bottom: 30px;
    }
}

.h-section-about__subinfo-tel {
    font-size: 14px;
    line-height: 25px;
    padding-left: 27px;
    position: relative;
    color: #000066;
    font-style: italic;
}

.h-section-about__subinfo-mail {
    font-size: 14px;
    line-height: 25px;
    padding-left: 27px;
    position: relative;
    color: #000066;
    font-style: italic;
}

.h-section-about__subinfo-tel-ico,
.h-section-about__subinfo-mail-ico {
    fill: #000066;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

.h-section-about__subinfo-tel-ico {
    left: 2px;
}

.h-section-about__subinfo-mail-ico {
    top: 55%;
}

.about-abilities {
    height: 100%;
    display: flex;
    padding-left: 6.35%;
}

.about-abilities__col {
    height: 100%;
    flex: 0 0 auto;
    width: 23%;
    margin-right: 7%;
    display: flex;
    flex-direction: column;

    &-2 {
        margin-right: 8%;
    }

    &-3 {
        width: 27%;
    }

    &:last-child {
        margin-right: 0;
    }
}

.about-abilities__title {
    flex: 0 0 auto;
    font-size: 20px;
    margin-bottom: 20px;
    color: #000066;
}

.about-abilities__list-c {
    flex: 1;
    overflow: auto;
    width: 100%;
    position: relative;

    &.scroll-content.scroll-scrolly_visible {
        padding-right: 20px;
    }

    &.about-abilities__list-c_shadow {
        &:before {
            position: absolute;
            display: block;
            content: "";
            left: 0;
            right: 20;
            bottom: 0;
            height: 45px;
            background: linear-gradient(to top, #fff 25%, rgba(#fff, 0) 100%);
            z-index: 1;
            pointer-events: none;
        }
    }

    .about-abilities__list-c {
        &::before {
            display: none;
        }
    }
}

.about-abilities__list {
    padding-bottom: 30px;
}

.about-abilities__item {
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 20px;
    padding-left: 20px;
    position: relative;

    &:before {
        position: absolute;
        content: "";
        display: block;
        top: -10px;
        right: -15px;
        left: 0;
        bottom: -10px;
    }

    &:last-child {
        margin-bottom: 0;

        &:before {
            bottom: 0;
        }
    }
}

.about-abilities__item-ico {
    fill: #000000;
    position: absolute;
    display: block;
    left: 1px;
    top: 8px;
}

.h-section-about__bg-img-container {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    text-align: right;
}

.h-section-about__bg-img {
    height: 100%;
    width: auto;

    &.h-section-about__bg-img_mobile-visible {
        display: none;
    }
}

.h-section-about__bg-img-container {
    &.h-section-about__col-r-bg-img {
        display: none;
    }
}

.h-section-about__col-r-in {
    height: 100%;
}

.h-section-about__col-r-cirlce-border-c {
    display: none;
}

.h-section-about__mobile-title {
    display: none;
}

.h-section-about__mobile-info {
    height: 100%;

    .h-section-about__subinfo,
    .h-section__desc,
    .about-mobile-info__toggler,
    .svg-icon-about-white-border-circle-mobile
    {
        display: none;
    }
}

.about-mobile-info__collapsed {
    height: 100%;
}

@media only screen and (max-width: 1365px) {
    .h-section__title {
        &.h-section__title_about {
            font-size: 60px;
            line-height: 70px;
        }
    }

    .h-section__info {
        &.h-section__info_about {
            margin-top: 35px;
        }
    }

    .h-section__desc {
        &.h-section__desc_about {
            max-width: 100%;
            margin-bottom: 10px;
        }
    }

    .h-section-about__col-l,
    .h-section-about__col-r {
        flex: 0 0 auto;
        width: 50%;
        max-width: 50%;
        height: 100%;
    }

    .h-section-about__col-l {
        z-index: 1;
        padding-right: 10px;
    }

    .h-section-about__col-r {
        overflow: auto;
        overflow: visible !important;
        height: 100% !important;
        width: 100%;
        max-width: 100%;
        margin-left: -50%;
    }

    .h-section-about__col-r-in {
        width: 100%;
        padding-left: calc(50% + 10px);
        height: auto;
        position: relative;

        &:after {
            position: relative;
            display: block;
            height: 90px;
            width: 100%;
            content: "_";
            font-size: 1px;
            color: transparent;
        }
    }

    .h-section-about__mobile-info,
    .about-mobile-info__collapsed {
        height: auto;
    }

    .h-section-about__row-wrap {
        flex-direction: row;
        height: 100%;
    }

    .h-section-about__spacer-row {
        display: none;
    }

    .h-section-about__bg-img {
        height: 100%;
        width: auto;
        position: absolute;
        bottom: 0;
        right: 0;
    }

    .h-section-about__bg-img-container {
        width: 100%;
        height: 100%;
        flex: 0 0 auto;
        position: relative;
        top: 0;
        left: 0;
        bottom: auto;
        right: auto;
        pointer-events: all;
    }

    .h-section-about__bg-img-container {
        display: none;

        &.h-section-about__col-r-bg-img {
            display: block;
        }
    }

    .h-section-about__col-r-cirlce-border-c {
        display: block;
        position: relative;
        width: 100%;
    }

    .h-section-about__col-r-cirlce-border-in {
        width: 100%;
        padding-top: 26%;
        position: absolute;
        transform: translateY(-80%);
    }

    .h-section-about__col-r-cirlce-border {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        fill: #fff;
    }

    .about-abilities {
        position: relative;
        padding-left: 0;
        flex-direction: column;
        height: auto;
        padding-top: 35px;
    }

    .about-abilities__col {
        flex: 0 0 auto;
        width: 100%;
        margin-right: 0;
        height: auto;
    }

    .about-abilities__list {
        padding-bottom: 0;
        margin-bottom: 35px;
    }

    .about-abilities__list-c {
        flex: auto;
        overflow: visible;
    }

    .about-abilities__item {
        padding-left: 15px;
    }

    .about-abilities__title {
        margin-bottom: 15px;
    }
}

@media only screen and (max-width : 767px) {
    .h-section-about {
        &:not(:last-child) {
            padding: 60px 20px 0;

            .h-section-about__col-r-in {
                &:before {
                    display: block;
                    content: "";
                    position: absolute;
                    bottom: 0;
                    left: 20px;
                    right: 20px;
                    height: 1px;
                    background: #dddddd;
                }
            }
        }

        &:last-child {
            padding: 0 20px 60px;

            .h-section-about__col-r-in {
                &:after {
                    display: block;
                    height: 30px;
                }
            }
        }
    }

    .h-section-about__col-l {
        display: none;
    }

    .h-section-about__col-r {
        height: auto !important;
        margin-left: -20px;
        margin-right: -20px;
        max-width: calc(100% + 40px);
        width: calc(100% + 40px);
    }

    .h-section-about__col-r-in {
        padding: 0 20px 20px;
        min-height: calc(100vh - 60px);

        &:after {
            display: none;
        }
    }

    .h-section-about__bg-img {
        &.h-section-about__bg-img_mobile-visible {
            display: block;
        }

        &.h-section-about__bg-img_mobile-hide {
            display: none;
        }
    }

    .h-section-about__bg-img-container {
        margin-top: 60px;
        margin-bottom: -70px;
        margin-left: -20px;
        margin-right: -20px;
        width: auto;
        height: calc(100vh - 60px);
    }

    .h-section-about__col-r-cirlce-border-c,
    .h-section-about__col-r-cirlce-border {
        display: none;
        width: auto;
        margin: 0 -20px;
    }

    .h-section-about__col-r-cirlce-border-in {
        padding-top: 40%;
        transform: translateY(-40%);
    }

    .h-section-about__col-r-cirlce-border-mobile {
        display: block;
        fill: #fff;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
    }

    .h-section-about__mobile-info {
        .h-section-about__mobile-title,
        .h-section-about__subinfo,
        .h-section__desc,
        .about-mobile-info__toggler,
        .svg-icon-about-white-border-circle-mobile,
        .h-section-about__col-r-cirlce-border-c
        {
            display: block;
        }

        .h-section-about__subinfo {
            margin-bottom: 35px;
        }
    }    
   
    .h-section-about__mobile-title {
        display: block;
        position: absolute;
        top: 35px;
        left: 20px;
        right: 20px;
        font-size: 40px;
        line-height: 50px;
        color: $c-darkblue;
        z-index: 1;
    }

    .h-section-about__mobile-info, 
    .about-mobile-info__collapsed {
        position: relative;
    }

    .about-mobile-info__button-wrap {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .about-mobile-info__toggler {
        display: block;
        outline: none;
        border: none;
        margin: 0;
        padding: 0;
        border-radius: 0;
        cursor: pointer;
        text-align: center;
        font-size: 14px;
        font-style: italic;
        height: 35px;
        line-height: 35px;
        position: relative;
        border-radius: 5px;

        &:hover,
        &:focus,
        &:active {
            .about-mobile-info__toggler-front {
                background-color: rgb(30, 30, 105);
                box-shadow: 0px 10px 15px 0px rgba(172, 197, 255, 0.4);
            }

            .about-mobile-info__toggler-back  {
                border-color: #cccccc;
            }
        }

        &.active {
            .about-mobile-info__toggler-back {
                display: block;
            }

            .about-mobile-info__toggler-front {
                display: none;
            }
        }
    }

    .about-mobile-info__toggler-back {
        display: none;
        color: #767676;
        background: #ededed;
        padding-left: 20px;
        padding-right: 40px;
        min-width: 130px;
        height: 100%;
        border-radius: 5px;
        transition: all .3s;
        border: 1px solid transparent;
    
        &:before {
            position: absolute;
            display: block;
            content: "";
            width: 8px;
            height: 8px;
            border: 1px solid #767676;
            border-top: none;
            border-right: none;
            right: 18px;
            top: 16px;
            transform: rotate(135deg);
            transition: all .3s;
        }
    }

    .about-mobile-info__toggler-front {
        display: block;
        color: #fff;
        background: $c-darkblue;
        padding-left: 20px;
        padding-right: 40px;
        min-width: 200px;
        height: 100%;
        border-radius: 5px;
        transition: all .3s;
    
        &:before {
            position: absolute;
            display: block;
            content: "";
            width: 8px;
            height: 8px;
            border: 1px solid #fff;
            border-top: none;
            border-right: none;
            right: 18px;
            top: 10px;
            transform: rotate(-45deg);
            transition: all .3s;
        }
    }

    .about-mobile-info__collapsed {
        max-height: 0;
        overflow: hidden;
        transition: all .5s ease-in-out;

        &.active {
            max-height: 2000px;
        }
    }

    .about-abilities__list {
        margin-bottom: 0;
        padding-bottom: 40px;
    }

    .h-section-about__col-wrap,
    .h-section-about__col-wrap > .col-8,
    .h-section-about__row-wrap {
        height: auto;
    }
}