body {
	font-weight: 400;
	font-family: $f-PTSerif;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	font-family: inherit;
	font-weight: inherit;
	line-height: inherit;
	color: inherit;
	font-size: inherit;
}

p {
	&:last-child {
		margin-bottom: 0;
	}
}

label {
	margin-bottom: 0;
	font-weight: normal;
	cursor: pointer;
}

ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

a,
a:hover,
a:focus {
	text-decoration: none;
	color: inherit;
	outline: none;
}

.html-area {
	a {}

	ul {
		li {}
	}

	p {}

	span {}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {}

	h1 {}

	h2 {}

	h3 {}

	h4 {}

	h5 {}

	h6 {}
}