.fp-section.fp-table, 
.fp-slide.fp-table {
    display: flex;
}

.fp-tableCell {
    display: flex;
    flex-direction: column;
}

#fp-nav.fp-right {
    right: 43px;
    margin-top: 0 !important;
    transform: translateY(-50%);
}

#fp-nav {
    ul {
        li {
            display: block;
            width: auto;
            height: auto;
            margin: 0;
            position: relative;

            a {
                padding: 12px;
                position: relative;

                span {
                    display: none;
                }

                &:before {
                    position: absolute;
                    content: "";
                    display: block;
                    top: 50%;
                    transform: translateY(-50%);
                    border-radius: 50%;
                    width: 4px;
                    height: 4px;
                    background: #a5a19f;
                    transition: all 0.3s;
                }

                &.active {
                    &:before {
                        width: 4px;
                        height: 4px;
                        background: #fff;
                        border: none;
                        margin-top: 0;
                        left: auto;
                    }
                }
            }
        }
    }

    &.onepage-pagination_blue {
        ul {
            li {
                a {
                    &:before {
                        background: #a2a2a2;
                    }

                    &.active {
                        &:before {
                            background: $c-darkblue;
                        }
                    }
                }
            }
        }
    }
}